const F=[["Self-healing","Detects failures, guarded rollback, post-validation"],
["Observability","Health, latency p50/p90/p99, SLO burn, daily/weekly"],
["Payments","Stripe links + webhook automation, 7-day trials"],
["Security","HSTS, CSP, rate limit, signature checks, honeypots"],
["Integrations","Notion, Telegram, Stripe, GitHub, Replit"],
["Zero babysitting","Autonomous loop every 5 minutes"]]
export default function FeatureGrid(){
  return <section className="grid cards">
    {F.map(([t,d])=> <div className="card" key={t}><h3>{t}</h3><p style={{opacity:.85}}>{d}</p></div>)}
  </section>
}
